.Main {
    display: grid;
    grid-template-columns:  repeat(3, 1fr);
    grid-template-rows:  (30%,70%);
}

.text {
    grid-column: 2/3;
    text-align: center;
    line-height: 1.5;
    padding-left: 10%;
    padding-right: 10%;
}

.signForm {
    grid-row: 2/2;
    grid-column: 2/2;
}